rbug.com/546034\n        > .flex { min-width: 0;  }\n    }\n\n    .layout#{$name}-column {\n        > .#{$flexName}-33 { flex: 1 1 100%;  max-width: 100%;  max-height: 33.33%; box-sizing: border-box; }\n\n        > .#{$flexName}-66 { flex: 1 1 100%;  max-width: 100%;  max-height: 66.66%; box-sizing: border-box; }\n\n        // Required by Chrome M48+ due to http://crbug.com/546034\n        > .flex { min-height: 0; }\n    }\n}\n\n@mixin layout-align-for-name($suffix: null) {\n\n    // Alignment attributes for layout containers' children\n    // Arrange on the Main Axis\n    // center, start, end, space-between, space-around\n    // flex-start is the default for justify-content\n    // ------------------------------\n\n    $name: 'layout-align';\n    @if $suffix != null {\n        $name: 'layout-align-#{$suffix}';\n    }\n\n    .#{$name},\n    .#{$name}-start-stretch // defaults\n    {\n        justify-content : flex-start;\n        align-content : stretch;\n        align-items: stretch;\n    }\n\n    // Main Axis Center\n    .#{$name}-start,\n    .#{$name}-start-start,\n    .#{$name}-start-center,\n    .#{$name}-start-end,\n    .#{$name}-start-stretch\n    {\n        justify-content: flex-start;\n    }\n\n    // Main Axis Center\n    .#{$name}-center,           //stretch\n    .#{$name}-center-start,\n    .#{$name}-center-center,\n    .#{$name}-center-end,\n    .#{$name}-center-stretch\n    {\n        justify-content: center;\n    }\n\n    // Main Axis End\n    .#{$name}-end, //stretch\n    .#{$name}-end-start,\n    .#{$name}-end-center,\n    .#{$name}-end-end,\n    .#{$name}-end-stretch\n    {\n        justify-content: flex-end;\n    }\n\n    // Main Axis Space Around\n    .#{$name}-space-around, //stretch\n    .#{$name}-space-around-center,\n    .#{$name}-space-around-start,\n    .#{$name}-space-around-end,\n    .#{$name}-space-around-stretch\n    {\n        justify-content: space-around;\n    }\n\n    // Main Axis Space Between\n    .#{$name}-space-between, //stretch\n    .#{$name}-space-between-center,\n    .#{$name}-space-between-start,\n    .#{$name}-space-between-end,\n    .#{$name}-space-between-stretch\n    {\n        justify-content: space-between;\n    }\n\n\n    // Arrange on the Cross Axis\n    // center, start, end\n    // stretch is the default for align-items\n    // ------------------------------\n\n    // Cross Axis Start\n    .#{$name}-start-start,\n    .#{$name}-center-start,\n    .#{$name}-end-start,\n    .#{$name}-space-between-start,\n    .#{$name}-space-around-start\n    {\n        align-items: flex-start;\n        align-content: flex-start;\n    }\n\n    // Cross Axis Center\n    .#{$name}-start-center,\n    .#{$name}-center-center,\n    .#{$name}-end-center,\n    .#{$name}-space-between-center,\n    .#{$name}-space-around-center\n    {\n        align-items: center;\n        align-content: center;\n        max-width: 100%;\n    }\n\n    // Cross Axis Center IE overflow fix\n    .#{$name}-start-center > *,\n    .#{$name}-center-center > *,\n    .#{$name}-end-center > *,\n    .#{$name}-space-between-center > *,\n    .#{$name}-space-around-center > *\n    {\n        max-width: 100%;\n        box-sizing: border-box;\n    }\n\n    // Cross Axis End\n    .#{$name}-start-end,\n    .#{$name}-center-end,\n    .#{$name}-end-end,\n    .#{$name}-space-between-end,\n    .#{$name}-space-around-end\n    {\n        align-items: flex-end;\n        align-content: flex-end;\n    }\n\n    // Cross Axis Start\n    .#{$name}-start-stretch,\n    .#{$name}-center-stretch,\n    .#{$name}-end-stretch,\n    .#{$name}-space-between-stretch,\n    .#{$name}-space-around-stretch\n    {\n        align-items: stretch;\n        align-content: stretch;\n    }\n}\n\n@mixin layout-padding-margin() {\n\n    // NOTE: these`> *` selectors should only be applied for layout=\"row\" or layout=\"column\" children !!\n    .layout-padding-sm > *,\n    .layout-padding    > .flex-sm\n    {\n        padding: $layout-gutter-width / 4;\n    }\n\n    .layout-padding,\n    .layout-padding-gt-sm,\n    .layout-padding-md,\n\n        // NOTE: these`> *` selectors should only be applied for layout=\"row\" or layout=\"column\" children !!\n    .layout-padding        > *,\n    .layout-padding-gt-sm  > *,\n    .layout-padding-md     > *,\n\n    .layout-padding        > .flex,\n    .layout-padding        > .flex-gt-sm,\n    .layout-padding        > .flex-md\n    {\n        padding: $layout-gutter-width / 2;\n    }\n\n    // NOTE: these`> *` selectors should only be applied for layout=\"row\" or layout=\"column\" children !!\n    .layout-padding-gt-md  > *,\n    .layout-padding-lg     > *,\n    .layout-padding-gt-lg  > *,\n\n    .layout-padding        > .flex-gt-md,\n    .layout-padding        > .flex-lg,\n    .layout-padding        > .flex-gt-lg\n    {\n        padding: $layout-gutter-width / 1;\n    }\n\n    // Margin enhancements\n\n    .layout-margin-sm      > *,\n    .layout-margin         > .flex-sm\n    {\n        margin: $layout-gutter-width / 4;\n    }\n\n    .layout-margin,\n    .layout-margin-gt-sm,\n    .layout-margin-md,\n\n        // NOTE: these`> *` selectors should only be applied for layout=\"row\" or layout=\"column\" children !!\n    .layout-margin         > *,\n    .layout-margin-gt-sm   > *,\n    .layout-margin-md      > *,\n\n    .layout-margin         > .flex,\n    .layout-margin         > .flex-gt-sm,\n    .layout-margin         > .flex-md\n    {\n        margin: $layout-gutter-width / 2;\n    }\n\n    // NOTE: these`> *` selectors should only be applied for layout=\"row\" or layout=\"column\" children !!\n    .layout-margin-gt-md  > *,\n    .layout-margin-lg     > *,\n    .layout-margin-gt-lg  > *,\n\n    .layout-margin        > .flex-gt-md,\n    .layout-margin        > .flex-lg,\n    .layout-margin        > .flex-gt-lg\n    {\n        margin: $layout-gutter-width / 1;\n    }\n\n    .layout-wrap {\n        flex-wrap: wrap;\n    }\n\n    .layout-nowrap {\n        flex-wrap: nowrap;\n    }\n\n    .layout-fill {\n        margin: 0;\n        width: 100%;\n        min-height: 100%;\n        height: 100%;\n    }\n}\n\n@mixin layouts_for_breakpoint($name:null) {\n\n    @include flex-order-for-name($name);\n    @include offset-for-name($name);\n    @include layout-align-for-name($name);\n\n    @include flex-properties-for-name($name);\n    @include layout-for-name($name);\n}\n","$vertical-padding: 12px;\n$horizontal-padding: 24px;\n\n@mixin mc-panel-element-padding() {\n    padding: $vertical-padding $horizontal-padding;\n}\n\n.mc-panel {\n    display: flex;\n    flex-direction: column;\n    border-radius: 3px;\n\n    &__header {\n        @include mc-panel-element-padding();\n        display: flex;\n    }\n\n    &__title {\n        display: block;\n        flex-grow: 1;\n    }\n\n    &__actions {\n        display: block;\n        flex-shrink: 0;\n    }\n\n    &__content {\n        @include mc-panel-element-padding();\n        display: block;\n    }\n\n    &__footer {\n        @include mc-panel-element-padding();\n        display: flex;\n    }\n}\n","\n// TODO\n*,\n*::before,\n*::after {\n    box-sizing: border-box; // 1\n}\n\nhtml {\n    -webkit-text-size-adjust: 100%; // 2\n    -ms-text-size-adjust: 100%; // 2\n\n    -ms-overflow-style: scrollbar; // 3\n\n    -moz-osx-font-smoothing: grayscale;\n\n    // Helps fonts on OSX looks more consistent with other systems\n    // Isn't currently in button styles due to performance concerns\n    -webkit-font-smoothing: antialiased;\n    -webkit-tap-highlight-color: rgba(0, 0, 0, 0); // 4\n    -webkit-touch-callout: none;\n    scroll-behavior: smooth;\n}\n\n// Body\n//\n// 1. remove the margin in all browsers.\n\nbody {\n    display: flex;\n    min-height: 100vh;\n    min-width: calc(100vw - (100vw - 100%) ); // cutting out vertical scrollbar\n    font-family: map-get(map-get($fonts, 'base'), font-family);\n    margin: 0; // 1\n}\n\na {\n    display: block;\n    outline: none;\n    text-decoration: none;\n    color: inherit;\n}\n\n.flex-spacer {\n    flex-grow: 1;\n}\n\ncode, pre {\n    font-family: 'Roboto Mono', monospace;\n}\n\ncode {\n    font-size: 90%;\n}\n\npre {\n    font-size: 14px;\n}\n","// Styles for overview and guide docs\n.docs-markdown {\n    max-width: 100%;\n\n    &__p, &__li {\n        margin: 0;\n        padding-top: 8px;\n    }\n\n    &__img {\n        display: flex;\n        max-width: 100%;\n        padding-bottom: 4px;\n    }\n\n    &__a {\n        display: initial;\n        text-decoration: none;\n        cursor: pointer;\n    }\n\n    &__ul {\n        position: relative;\n        list-style: none;\n        margin: 0;\n        padding: 0;\n\n        & .docs-markdown__li {\n            padding-left: 20px;\n        }\n\n        & .docs-markdown__li:before {\n            content:  \"—\";\n            position: absolute;\n            left: 0;\n        }\n    }\n\n    &__ol {\n        margin: 0;\n        padding-left: 32px;\n\n        & .docs-markdown__li {\n            padding-left: 4px;\n        }\n\n        & & {\n            list-style-type: lower-alpha;\n            padding-left: 16px;\n        }\n    }\n\n    &__table {\n        text-align: left;\n        padding-top: 16px;\n    }\n\n    &__th, &__td {\n        padding: 8px;\n        vertical-align: top;\n\n        &:first-child {\n            padding-left: 16px;\n        }\n\n        &:last-child {\n            padding-right: 16px;\n        }\n    }\n\n    &__code {\n        white-space: pre-wrap;\n    }\n\n    &__pre>&__code {\n        display: block;\n        position: relative;\n        overflow-x: auto;\n        padding: 12px;\n    }\n\n    &-code-block {\n        display: flex;\n        position: absolute;\n        top: 0;\n        right: 0;\n        z-index: 10;\n        padding: 6px 6px;\n\n        &__copied {\n            display: none;\n            font-size: 14px;\n        }\n\n        &_success {\n            .docs-markdown-code-block__copied {\n                display: block;\n            }\n\n        