 renders all of the core styles that depend on the theme.\n@mixin mc-core-theme($theme) {\n    @include mc-pseudo-checkbox-theme($theme);\n\n    .mc-app-background {\n        $background: map-get($theme, background);\n        $foreground: map-get($theme, foreground);\n\n        background-color: mc-color($background, background);\n        color: mc-color($foreground, text);\n    }\n\n    .mc-theme-loaded-marker {\n        display: none;\n    }\n}\n","@import '../../../design-tokens/index';\n\n\n@mixin popup-params($theme) {\n    $popup: map-get(map-get($theme, components), popup);\n\n    box-shadow: map-get($popup, shadow);\n    border-color: map-get($popup, border);\n\n    background-color: map-get($popup, background);\n}\n","@import '../core/styles/common/popup';\n@import '../core/theming/theming';\n\n\n@mixin mc-autocomplete-theme($theme) {\n    $foreground: map-get($theme, foreground);\n\n    .mc-autocomplete-panel {\n        @include popup-params($theme);\n\n        color: mc-color($foreground, text);\n\n        .mc-selected {\n            $popup: map-get(map-get($theme, components), popup);\n\n            background-color: map-get($popup, background);\n        }\n    }\n}\n","@import '../design-tokens/index';\n@import '../core/theming/theming';\n\n\n@mixin card-type($color, $background, $shadow-color) {\n    box-shadow:\n        inset -1px 0 0 0 $shadow-color,\n        inset 0 1px 0 0 $shadow-color,\n        inset 0 -1px 0 0 $shadow-color;\n\n    background-color: $background;\n\n    border-left-color: $color;\n}\n\n@mixin mc-card-theme($theme) {\n    $foreground: map-get($theme, foreground);\n    $background: map-get($theme, background);\n\n    $second: map-get($theme, second);\n    $error: map-get($theme, error);\n    $info: map-get($theme, info);\n    $warning: map-get($theme, warning);\n    $success: map-get($theme, success);\n\n    $popup: map-get(map-get($theme, components), popup);\n\n    $is-dark: map-get($theme, is-dark);\n\n    .mc-card {\n        color: map-get($foreground, text);\n\n        &.mc-card_info {\n            @if($is-dark) {\n                @include card-type(mc-color($info), mc-color($info, darker), mc-color($second, darker));\n            } @else {\n                @include card-type(mc-color($info), mc-color($info, lighter), mc-color($second, lighter));\n            }\n        }\n\n        &.mc-card_warning {\n            @if($is-dark) {\n                @include card-type(mc-color($warning), mc-color($warning, darker), mc-color($second, darker));\n            } @else {\n                @include card-type(mc-color($warning), mc-color($warning, lighter), mc-color($second, lighter));\n            }\n        }\n\n        &.mc-card_success {\n            @if($is-dark) {\n                @include card-type(mc-color($success), mc-color($success, darker), mc-color($second, darker));\n            } @else {\n                @include card-type(mc-color($success), mc-color($success, lighter), mc-color($second, lighter));\n            }\n        }\n\n        &.mc-card_error {\n            @if($is-dark) {\n                @include card-type(mc-color($error), mc-color($error, darker), mc-color($second, darker));\n            } @else {\n                @include card-type(mc-color($error), mc-color($error, lighter), mc-color($second, lighter));\n            }\n        }\n\n        &.mc-card_white {\n            background-color: map-get($popup, background);\n        }\n\n        &.mc-selected {\n            background-color: map-get(map-get($theme, states), selected-color);\n        }\n\n        &:not(.mc-card_readonly):hover {\n            .mc-card__overlay {\n                background: map-get($background, overlay-hovered);\n            }\n        }\n\n        &.cdk-keyboard-focused {\n            box-shadow: 0 0 0 2px map-get(map-get($theme, states), focused-color);\n        }\n    }\n}\n","@mixin user-select($value) {\n    -webkit-user-select: $value;\n    -moz-user-select: $value;\n    -ms-user-select: $value;\n    user-select: $value;\n}\n\n@mixin input-placeholder {\n    &::placeholder {\n        @content;\n    }\n\n    &::-moz-placeholder {\n        @content;\n    }\n\n    &::-webkit-input-placeholder {\n        @content;\n    }\n\n    &:-ms-input-placeholder {\n        @content;\n    }\n}\n\n@mixin cursor-grab {\n    cursor: -webkit-grab;\n    cursor: grab;\n}\n\n@mixin cursor-grabbing {\n    cursor: -webkit-grabbing;\n    cursor: grabbing;\n}\n","@import '../core/theming/theming';\n@import '../core/styles/typography/typography-utils';\n\n\n@mixin mc-icon-theme($theme) {\n    $foreground: map-get($theme, foreground);\n\n    $primary: map-get($theme, primary);\n    $second: map-get($theme, second);\n    $error: map-get($theme, error);\n\n    $primary-color: mc-color($primary, 400);\n\n    // Дефолтные серые иконки default-icon: лупа в поле, иконки дропдаунов v, все иконки вне полей.\n    .mc-icon:not(.mc-icon_light) {\n        color: mc-color($foreground, icon);\n\n        &.mc-primary {\n            color: mix(mc-color($foreground, icon), $primary-color, 30%);\n\n            &[disabled],\n            &.mc-disabled {\n                color: $primary-color;\n            }\n        }\n\n        &.mc-second {\n            color: map-get($foreground, text-less-contrast);\n\n            &[disabled],\n            &.mc-disabled {\n                color: mc-color($second);\n            }\n        }\n\n        &.mc-error {\n            color: mix(mc-color($foreground, icon), mc-color($error), 30%);\n\n            &[disabled],\n            &.mc-disabled {\n                color: mc-color($error);\n            }\n        }\n\n        &:not(.mc-primary):not(.mc-second):not(.mc-error) {\n            &[disabled],\n            &.mc-disabled {\n                color: mc-color($second);\n\n                cursor: default;\n            }\n        }\n    }\n\n    // Облегченные серые иконки less-contrast-icon нужны тогда, когда действие, которое они делают не дефолтное и\n    // не самое важное. Не используются вне инпутов или селектов.\n    .mc-icon.mc-icon_light {\n        &.mc-primary {\n            color: $primary-color;\n\n            &:hover {\n                color: mix(map-get($foreground, text), $primary-color);\n            }\n        }\n\n        &.mc-second {\n            color: mc-color($second);\n\n            &:hover {\n                color: mix(map-get($foreground, text), mc-color($second), 90%);\n            }\n        }\n\n        &.mc-error {\n            color: mc-color($error);\n\n            &:hover {\n                color: mix(map-get($foreground, text), mc-color($error));\n            }\n        }\n    }\n}\n\n@mixin mc-icon-typography($config) {\n    .mc-icon {\n        font: {\n            size:   mc-font-size($config, button);\n            family: mc-font-family($config, button);\n            weight: mc-font-weight($config, button);\n        }\n    }\n}\n\n","@import '../core/theming/theming';\n@import '../core/styles/typography/typography-utils';\n\n\n@mixin mc-navbar-theme($theme) {\n    $navbar: map-get(map-get($theme, components), navbar);\n\n    .mc-navbar {\n        background-color: map-get($navbar, background);\n\n        .mc-navbar-title,\n        .mc-icon {\n            color: map-get($navbar, color);\n        }\n    }\n\n    .mc-navbar-brand {\n        .mc-navbar-title {\n            opacity: map-get($navbar, opacity);\n        }\n    }\n\n    .mc-navbar-item {\n        $navbar-item: map-get(map-get($theme, components), navbar-item);\n\n        outline: 0;\n\n        color: map-get($navbar-item, color);\n\n        &:not([disabled]) {\n            outline: 0;\n\n            &.cdk-keyboard-focused {\n                box-shadow: inset 0 0 0 2px map-get(map-get($theme, states), focused-color);\n            }\n        }\n\n        &.mc-navbar-item-active {\n            background-color: map-get($navbar-item, states-active);\n        }\n\n        &:hover:not([disabled]):not(.mc-progress) {\n            background-color: map-get($navbar-item, states-hover);\n        }\n\n        &.mc-progress:hover:not([disabled]) {\n            background-color: map-get($navbar-item, states-progress);\n        }\n\n        &[disabled] {\n            .mc-navbar-title,\n            .mc-icon {\n                opacity: map-get($navbar-item, states-disabled-opacity);\n            }\n        }\n    }\n}\n\n@mixin mc-navbar-typography($config) {\n    .mc-navbar-title {\n        @include mc-typography-level-to-styles($config, body);\n    }\n\n    .mc-navbar-brand {\n        .mc-navbar-title {\n            @include mc-typography-level-to-styles($config, title);\n        }\n    }\n}\n","@import '../theming/theming';\n\n\n@mixin mc-panel-theme($theme) {\n    $background: map-get($theme, background);\n\n    $second: map-get($theme, second);\n\n    $is-dark: map-get($theme, is-dark);\n\n    .mc-panel {\n        background-color: map-get($background, background);\n        border: 1px solid mc-color($second, if($is-dark, 700, 60));\n    }\n}\n","@import \"../core/theming/theming\";\n\n\n@mixin mc-progress-bar-theme($theme) {\n    $primary: map-get($theme, primary);\n    $second: map-get($theme, second);\n    $error: map-get($theme, error);\n\n    $progress-bar: map-get(map-get($theme, components), progress-bar);\n\n    .mc-progress-bar {\n        background-color: map-get($progress-bar, background);\n\n        &.mc-primary .mc-progress-bar__line {\n            background-color: mc-color($primary);\n        }\n\n        &.mc-second .mc-progress-bar__line {\n            background-color: mc-color($second);\n        }\n\n        &.mc-error .mc-progress-bar__line {\n            background-color: mc-color($error);\n        }\n    }\n}\n","@import \"../core/theming/theming\";\n\n\n@mixin mc-progress-spinner-theme($theme) {\n    $primary: map-get($theme, primary);\n    $second: map-get($theme, second);\n    $error: map-get($theme, error);\n\n    .mc-progress-spinner {\n        &.mc-primary .mc-progress-spinner__circle {\n            stroke: mc-color($primary);\n        }\n\n        &.mc-second .mc-progress-spinner__circle {\n            stroke: mc-color($second);\n        }\n\n        &.mc-error .mc-progress-spinner__circle {\n            stroke: mc-color($error);\n        }\n    }\n}\n","@mixin mc-splitter-theme($theme) {\n    $background: map-get($theme, background);\n\n    .mc-gutter {\n        cursor: col-resize;\n\n        &:hover,\n        &.mc-gutter_dragged {\n            background-color: mix(map-get($background, background-disabled), map-get($background, overlay-hovered));\n        }\n\n        &.mc-gutter_vertical {\n            cursor: row-resize;\n        }\n\n        &[disabled] {\n            background-color: mix(mc-color($background, overlay-disabled), map-get($background, overlay-hovered));\n\n            cursor: default;\n        }\n    }\n}\n","\n\n@mixin mc-highlight-theme($theme) {\n    .mc-highlight {\n        color: inherit;\n\n        font-weight: bold;\n        background-color: transparent;\n    }\n}\n","// mixin definition ; sets LTR and RTL within the same style call\n// @see https://css-tricks.com/almanac/properties/d/direction/\n\n@mixin rtl($prop, $ltr-value, $rtl-value) {\n    #{$prop}: $ltr-value;\n\n    [dir=rtl] & {\n        #{$prop}: $rtl-value;\n    }\n}\n\n@mixin rtl-prop($ltr-prop, $rtl-prop, $value, $reset-value) {\n    #{$ltr-prop}: $value;\n\n    [dir=rtl] & {\n        #{$ltr-prop}: $reset-value;\n        #{$rtl-prop}: $value;\n    }\n}\n\n// To reverse padding (top left bottom right) -> (top right bottom left)\n@function rtl-value($list) {\n    @if length($list) == 4 {\n        @return nth($list, 1) nth($list, 4) nth($list, 3) nth($list, 2)\n    }\n    @if length($list) == 5 {\n        @return nth($list, 1) nth($list, 4) nth($list, 3) nth($list, 2) nth($list, 5)\n    }\n    @return $list;\n}\n","@import '../../mosaic/core/theming/theming';\n\n\n@mixin mosaic-docs-app-theme($theme) {\n    $primary: map-get($theme, primary);\n    $accent: map-get($theme, accent);\n    $warn: map-get($theme, warn);\n    $background: map-get($theme, background);\n    $foreground: map-get($theme, foreground);\n    //$next-theme: mc-palette($map-red); Переменная $map-red не определена\n\n    .docs-app-background {\n        background: map-get($background, background);\n    }\n\n    .docs-footer {\n        background: map-get($primary, background);\n        color: map-get($primary, default-contrast);\n    }\n}\n","@mixin mc-main-layout-theme($theme) {\n    $background: map-get($theme, background);\n    $background-color: mc-color($background, background);\n\n    .main { background-color: $background-color; }\n}\n","@mixin mc-component-viewer-theme($theme) {\n    $foreground: map-get($theme, foreground);\n    $text: mc-color($foreground, text);\n\n    .title{\n        color: $text;\n    }\n}\n"],"sourceRoot":""}]);
